
import React,{useState,useEffect,Component} from "react";
import "./styleHome.css"
import  {ApisOk} from "../Interface/Interface";
import axios from "axios";
import Item from "../Item/Item";
import HomeHeader from "../HomeHeader/HomeHeader";
import "./styleHome.css"
import { useContext } from "react";
import { ContextOk } from "../../Context/Context";
import Carousel from "react-multi-carousel";
//import "react-multi-carousel/lib/styles.css";
import CarouselOk from "./CarouselOk";



import ReactDOM from 'react-dom';

const Home=()=>{
    
    const[list,setList]=useState<ApisOk[]>([])//ACA LE DIGO QUE EL USESTATE VA A SER [] Y EL TIPO ES ApisOk[] es un array de con los tipos de apiOK
    //o <ApisOk[]> o Array<ApisOk>
    const[listVote,setListVote]=useState<ApisOk[]>([])
    const[listYear,setListYear]=useState<ApisOk[]>([])

    const{imageOk,GetObject,GetVideos}=useContext(ContextOk) 
   
    

    const baseUrl:string='https://api.themoviedb.org/3'
    const apiKey:string='42fea80bd124c21e385adf6985bb6c61';
    const url:string=`${baseUrl}/discover/movie?sort_by=popularity.desc&api_key=${apiKey}`
    const urlVote:string=`${baseUrl}/discover/movie?include_adult=false&include_video=false&language=en-US&page=1&vote_average.gte=10&vote_average.lte=10&api_key=${apiKey}`
    const urlYear:string=`${baseUrl}/discover/movie?include_adult=false&include_video=false&language=en-US&page=1&primary_release_year=2023&api_key=${apiKey}`
// const urlVote:string=`${baseUrl}/discover/movie?sort_by=vote_average.desc&api_key=${apiKey}`
        //instalo axios

        const GetApi=async()=>{
            try{
                const res = await axios.get(url)
                const resData= res.data.results as Array<ApisOk> /*ApisOk[]*/ //entro al results y le digo q lo traiga como array de apisOK. podria ser ApisOk[] como arriba. data es del axios
                setList(resData)
                GetObject(resData[0])//pongo esto asi apenas arranca pagina ya le pasa una pelicula para q se ponga en el home hed
                GetVideos(resData[0].id,"home")
                console.log(resData,"resDats")
            }
            catch(err){
                console.log(err)
            }
        }

        const GetApiByVote=async()=>{
            try{
                const resVote= await axios.get(urlVote)
                const resDataVote:any=resVote.data.results as Array<ApisOk>
                setListVote(resDataVote)
                GetObject(resDataVote[0])
                GetVideos(resDataVote[0].id,"home")
            }catch(err){
                console.log(err)
            }
        }

        const GetApiByYear=async()=>{
            try{
                const resYear= await axios.get(urlYear)
                const resDataYear:any=resYear.data.results as Array<ApisOk>
                console.log(resDataYear, "datayear")
                setListYear(resDataYear)
                GetObject(resDataYear[0])
                GetVideos(resDataYear[0].id,"home")
            }catch(err){
                console.log(err)
            }
        }

        useEffect(()=>{
            GetApi()
            GetApiByVote()
            GetApiByYear()
        },[])

        
    
            const responsive = {
              desktop: {
                breakpoint: { max: 3000, min: 1024 },
                items: 6,
                slidesToSlide: 6 // optional, default to 1.
              },
              tablet: {
                breakpoint: { max: 1024, min: 464 },
                items: 2,
                slidesToSlide: 2 // optional, default to 1.
              },
              mobile: {
                breakpoint: { max: 464, min: 0 },
                items: 1,
                slidesToSlide: 1 // optional, default to 1.
              }
            }
     

        //console.log("list", list)

       return(
           <div className="home-container">
               <HomeHeader/>
               <div className="item-container">
                <div className="item-home-title">
                    <h4>Top Movies</h4>
                    <div className="containerCarousel">
                        <Carousel 
                        
                        responsive={responsive}
                        
                        
                        >
                            {list.map((movie)=>{
                                return <Item dataOk={movie} key={movie.id} msg={"home"} />
                            })}
                        </Carousel>
             
                    </div>
                    </div>
                    <div className="item-home-title">
                    <h4>Most Popular</h4>
                   <div className="containerCarousel">
                        <Carousel 
                        
                        responsive={responsive}
                        
                        
                        >
                            {listVote.map((movie)=>{
                                console.log(movie, "movie")
                                if(movie.poster_path !== null){
                                    if(movie.vote_average==10){
                                return <Item dataOk={movie} key={movie.id} msg={"home"} />
                                }
                            }
                            })}
                        </Carousel>
                    </div>
                    </div>
                    <div className="item-home-title">
                    <h4>2023 Releases</h4>
                    <div className="containerCarousel">
                        <Carousel 
                        
                        responsive={responsive}
                        
                        
                        >
                            {listYear.map((movie:any)=>{
                                console.log(movie, "movie")
                                if(movie.poster_path !== null){
                                    
                                return <Item dataOk={movie} key={movie.id} msg={"home"} />
                              
                            }
                            })}
                        </Carousel>
                    </div>
                    </div>
               
               
         
               </div>
               <div>
           
               </div>
           </div>
       ) 


}

export default Home

