import React from "react"
import "./styleFooter.css"
import FacebookIcon from '@mui/icons-material/Facebook';
import InstagramIcon from '@mui/icons-material/Instagram';
import TwitterIcon from '@mui/icons-material/Twitter';
import YouTubeIcon from '@mui/icons-material/YouTube';
import { createTheme } from "@mui/material/styles";

const Footer=()=>{


    return(
        <div className="footerOk" style={{color:"white"}} >
            <div className="iconsfoot">
                <FacebookIcon/>
                <InstagramIcon/>
                <TwitterIcon/>
                <YouTubeIcon/>

            </div>           
            
            <div className="footerContent">
                <div className="textfoot">
                    <p>Audio descriptivo</p> 
                    <p>Relaciones con inversionistas</p>
                    <p>Avisos legales</p>
                </div>
                <div className="textfoot">
                    <p>Centro de ayuda</p> 
                    <p>Empleo</p>
                    <p>Preferencias de cookies</p>
                </div>
                <div className="textfoot">
                    <p>Tarjetas de regalo</p> 
                    <p>Terminos de uso</p>
                    <p>Informacion corporativa</p>
                </div>
                <div className="textfoot">
                    <p>Prensa</p> 
                    <p>Privacidad</p>
                    <p>Contactanos</p>
                </div>
           </div>
        </div>
    )
}

export default Footer